.videos {
  display: flex;
  flex-direction: column;
  gap: 24px;
  max-width: 1063px;
}
.videos__container {
  position: relative;
}
.videos__title {
  margin-bottom: 16px;

  color: var(--color--black);
  font-size: 24px;
  font-style: normal;
  font-weight: 500;
  line-height: 140%;
}

.swiper {
  min-width: 0;
  // width: 100%;
}
.swiper__btn {
  position: absolute;
  top: 45%;
  transform: translateY(-50%);
  // left: 0;
  background-color: transparent;
  z-index: 10;
  opacity: 0.8;
  
  & svg {fill: none;}

  &:hover { opacity: 1;}
}
.swiper-btn-prev {
  left: -20px;
}
.swiper-btn-next {
  right: -20px;
}