.video {
  @extend %flex-dc;
  --items: 4.2;
  flex-basis: calc((100% - var(--gap) * var(--items)) / var(--items)) !important;
  width: calc((100% - var(--gap) * var(--items)) / var(--items)) !important;
  max-width: 240px;
}
.video__img-wrap {
  @extend %relative;
  border-radius: 16px;
  overflow: hidden;
  flex-grow: 1;
}
.video__img-wrap::before,
.video__img-wrap::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;

  height: 4px;
}
.video__img-wrap::before {
  width: 100%;
  background-color: var(--color--accent-add);
}
.video__img-wrap::after {
  width: 32%;
  background-color: var(--color--accent);
  z-index: 1;
  overflow: hidden;
}
.video__img {
  width: 100%;
  aspect-ratio: 3 / 2;
  object-fit: cover;
  transition: 200ms linear;

  &:hover {
    scale: 1.1;
  }
}
.btn--settings {
  position: absolute;
  top: 8px;
  right: 8px;

  background-color: transparent
}
.btn--volume {
  position: absolute;
  bottom: 8px;
  right: 32px;

  background-color: transparent
}
.btn--crop {
  position: absolute;
  bottom: 8px;
  right: 8px;

  background-color: transparent
}
.btn-play {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  background-color: transparent
}
.time {
  position: absolute;
  bottom: 8px;
  left: 8px;

  display: flex;
  padding: 2px 4px;
  justify-content: center;
  align-items: center;

  color: #fff;
  font-size: 11px;
  font-weight: 400;
  line-height: 140%;

  border-radius: 4px;
  background: var(--color--accent);
}
.video__desc {
  @extend %flex-dc;
  gap: 4px;
  padding: 8px 0;
}
.video__title {
  height: 25px;
  color: var(--color--black);
  font-size: 18px;
  line-height: 140%;
  text-overflow: ellipsis;
  overflow: hidden;
}
.video__data {
  @extend %flex-ac;
  gap: 8px;
}
.video__data p {
  @extend %flex-ac;
  gap: 4px;

  color: var(--color--gray);
  font-size: 12px;
  line-height: 140%
}
.video__date {
  color: var(--color--gray);
  font-size: 12px;
  line-height: 140%
}
