.header {
  @extend %flex-ac;
  width: 100%;
  min-height: 80px;
  padding: 16px 0;

  color: var(--color--white);
  background-color: #fff;
  border-bottom: 1px solid var(--color--accent-add, #e8e2f4);
}

.header .container {
  @extend %flex-ac;
  justify-content: space-between;
}

// .header .logo {
//   @include breakpoint-min($tablet) {
//     margin-right: auto;
//   }
// }

.header__btn {
  display: flex;
  padding: 12px 12px 11px 12px;
  justify-content: center;
  align-items: center;
  color: #656466;
  font-size: 18px;
  font-weight: 400;
  line-height: 140%;

  border-radius: 50%;
  border: 1px solid var(--color--gray, #9f9ea1);
  background: var(--color--accent-add, #e8e2f4);
}

.header__search {
  display: flex;
  align-items: center;
  border-radius: 20px;
  border: 1px solid var(--color--gray, #9f9ea1);
}
.header__search input {
  display: flex;
  align-items: center;
  width: 440px;
  height: 40px;
  padding: 8px 24px;
  border-radius: 20px 0px 0px 20px;
  // border: 0.5px solid var(  --color--gray, #9F9EA1);

  color: #656466;

  font-size: 16px;
  font-weight: 400;

  &:focus { outline: 1px solid #5417D7;}
}
.header__search button {
  display: flex;
  width: 80px;
  padding: 8px 24px;
  justify-content: space-between;
  align-items: center;
  border-radius: 0px 20px 20px 0px;
  border-left: 1px solid var(  --color--gray, #9F9EA1);
  background: var(--color--accent-add, #e8e2f4);

  &:hover, &:focus { background: var(--color--accent, #5417D7); }
}
