:root {
  // ===== fonts =====
  --font--body: 'Roboto', sans-serif;
  --font--title: 'Roboto', sans-serif;

  // ===== colors =====
  --color--white: #ffffff;
  --color--black: #0A031B;
  --color--gray: #9F9EA1;
  --color--gray-dark: #656466;
  --color--accent: #5417D7;
  --color--accent-add: #E8E2F4;

  // ===== card set =====
  --items: 1;
  --gap: 12px;
  // ===== other =====
  --border-radius: 14px;
  --transition: 300ms linear;
}

// ===== breakpoints =====
$mobileS: 320px;
$mobileM: 375px;
$mobileL: 425px;
$mobile: 480px;
$tablet: 768px;
$laptop: 1024px;
$desktop: 1200px;
$desktopL: 1440px;
// ===== breakpoints =====