@import './reset';
@import './var';
// utils styles
@import './utils/mixins';
@import './utils/placeholders';
// components styles
@import './backdrop';
@import '../../components/loader/loader';

body {
  position: relative;

  display: flex;
  flex-direction: column;
  min-height: 100vh;

  font-size: 16px;
  line-height: 1;
  font-family: var(--font--body);
  color: var(--color--black);
  background-color: #F9F9F9;
}
main {
  flex-grow: 1;
}
footer {
  margin-top: auto;
}

svg {
  fill: currentColor;
  pointer-events: none;
}
button, a {
  @extend %cursor;
}

// .section {
//   padding: 16px 0;

//   @include breakpoint-min($tablet) {
//     padding: 32px 0;
//   }
// }
.container {
  width: 100%;
  padding: 0 32px;
  margin: 0 auto;

  @include breakpoint-min($mobile) {
    width: $mobile;
  }
  @include breakpoint-min($tablet) {
    width: $tablet;
  }
  @include breakpoint-min($desktop) {
    width: $desktop;
  }
  @include breakpoint-min($desktopL) {
    width: $desktopL;
    padding: 0 80px;
  }
}

.title {
  margin-bottom: 20px;

  font-family: var(--font--title);
  text-align: center;

  @include breakpoint-min($desktop) {
    font-size: 40px;
  }
}
.subtitle {
  font-family: var(--font--title);
  font-size: 24px;

  @include breakpoint-min($desktop) {
    font-size: 28px;
  }
}

.list {
  list-style: none;
}
.link {
  text-decoration: none;
  color: inherit;
}
.isHidden {
  display: none;
  pointer-events: none;
}