.sidebar {
  flex: 0 0 193px;
  padding-top: 50px;
}
.sidebar-nav__list {
  @extend %flex-dc;
}
.sidebar-nav__link {
  @extend %flex-ac;
  gap: 16px;
  padding: 8px 16px;
  border-radius: 8px;

  color: var(--color--gray-dark);
  font-size: 18px;
  line-height: 140%;

  &.current,
  &:hover {
    background: var(--color--accent-add);
  }

  & svg {
    flex: 0 0 24px;
  }
}